/* styles.css */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.container {
  text-align: center;
}

.countdown {
  background-color: #e6f2ff;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 20px;
}

.countdown-title {
  font-size: 18px;
  font-weight: bold;
  color: #2c3e50;
}

.countdown-timer {
  font-size: 24px;
  color: #2980b9;
  font-weight: bold;
}

.rankings {
  margin-bottom: 30px;
}

.top-rankings {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.top-rank-card {
  width: 30%;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  margin-bottom: 15px;
}

.top-rank-card-1 {
  background-color: #fff4cc;
  border: 3px solid #ffd700;
}

.top-rank-card-2 {
  background-color: #f0f0f0;
  border: 3px solid #silver;
}

.top-rank-card-3 {
  background-color: #ffe5b4;
  border: 3px solid #cd7f32;
}

.other-rankings .rank-item {
  display: flex;
  justify-content: space-between;
  background-color: #f8f9fa;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  align-items: center;
}

.form-section {
  background-color: #f1f1f1;
  padding: 20px;
  border-radius: 10px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  text-align: left;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.submit-btn {
  width: 100%;
  padding: 12px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-btn:hover {
  background-color: #2980b9;
}

/* 반응형 미디어 쿼리 */
@media screen and (max-width: 768px) {
  .top-rankings {
      flex-direction: column;
      align-items: center;
  }

  .top-rank-card {
      width: 100%;
      max-width: 400px;
  }

  .other-rankings .rank-item {
      flex-direction: column;
      text-align: center;
  }

  .other-rankings .rank-item span {
      margin-bottom: 5px;
  }

  body {
      padding: 10px;
  }

  h1 {
      font-size: 24px;
  }

  .countdown-title {
      font-size: 16px;
  }

  .countdown-timer {
      font-size: 20px;
  }
}

@media screen and (max-width: 480px) {
  .container {
      padding: 5px;
  }

  h1 {
      font-size: 20px;
  }

  .countdown-title {
      font-size: 14px;
  }

  .countdown-timer {
      font-size: 18px;
  }

  .form-group label {
      font-size: 14px;
  }

  .form-group input,
  .form-group select {
      padding: 8px;
  }

  .submit-btn {
      padding: 10px;
  }
}